/* Generic slick-slider styles */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: none; }

.slick-list:focus {
  outline: none; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0px;
  padding: 0px; }

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img {
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0px;
  top: 0px;
  display: table; }

.slick-track:after {
  content: "";
  display: table;
  clear: both; }

.slick-slide {
  float: left;
  min-height: 1px; }

.slick-slide img {
  display: block; }

.slick-slide a {
  display: block; }

.slick-slide {
  display: none; }

.slick-list.dragging img {
  pointer-events: none; }

.slick-initialized .slick-track .slick-slide {
  display: table-cell; }

.ie7 .carousel .slick-slide {
  display: block; }

.carousel {
  overflow: hidden;
  padding-bottom: 56px; }

/* Looped Carousels */
.carousel--looped {
  width: 100%; }
  .carousel--looped .banner {
    height: 485px;
    position: relative;
    display: block; }
  .carousel--looped .slick-dots {
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 50%;
    padding: 0;
    margin: 0;
    height: 56px;
    list-style-type: none;
    overflow: hidden; }
    .carousel--looped .slick-dots > li {
      float: left;
      position: relative;
      cursor: pointer;
      outline: none;
      padding: 0 8px;
      height: 56px;
      width: 16px; }
      .carousel--looped .slick-dots > li > button {
        -webkit-appearance: none;
        cursor: pointer;
        outline: none;
        border: none;
        background: none;
        font-size: 0;
        padding: 0;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width: 10px;
        height: 10px; }
